 import img from '../../static/img/3.jpg'
export default {
	data() {
		return {
			userInfo:{
				 img:img,
				 name:"吴彦祖",
				 unit:"小金县八角乡",
				 jue:"统治管理队员",
				 account:"13540021548",
				 sex:"M",
				 idCard:'511255488963251145',
				 phone:"13540025689",
				 creatTime:"2019-12-10"
			}
                 
		}
	},
	methods: {
		ontrueGetList() {
			uni.showToast({
				title: '个人中心'
			})
			console.log("加载了个人中心，可以把网络请求放这里")

		},
		//修改头像
		changeAvatar(){
			var _this=this;
			uni.chooseImage({
			    count: 1, //默认9
			    sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
			    // sourceType: ['album'], //从相册选择
			    success: function (res) {
					_this.userInfo.img=res.tempFilePaths[0]
			        console.log(JSON.stringify(res.tempFilePaths[0]));
			    }
			});
		},
		//修改手机号
		changePhone(){
			let data = {
				phone:this.userInfo.phone,
				
			};
			let url =  'change-phone';
			
			uni.navigateTo({
				url: `/pages/change-phone/${url}?data=${JSON.stringify(data)}`
			});
		},
		loginOut(){
			this.$refs.showtip.open();
		},
		cancel(type) {
		
			this.$refs.showtip.close();
			
		}

	}
}
